.formContainer{max-width:800px;margin:0 auto;padding:2rem 1rem}.formContainer .header{display:flex;justify-content:space-between;align-items:center;margin-bottom:2rem}.formContainer .header h1{font-size:2rem;color:var(--text-color);margin:0}.formContainer .header .backButton{padding:.5rem 1rem;background-color:#f3f4f6;border-radius:4px;transition:background-color .3s}.formContainer .header .backButton:hover{background-color:#e5e7eb;text-decoration:none}.formContainer .error{background-color:#fee2e2;border-left:4px solid #ef4444;color:#b91c1c;padding:1rem;margin-bottom:1.5rem;border-radius:4px}.formContainer .characterForm{background-color:#fff;border-radius:8px;box-shadow:0 1px 3px rgba(0,0,0,.1);overflow:hidden}.formContainer .characterForm .formSection{padding:1.5rem;border-bottom:1px solid #e5e7eb}.formContainer .characterForm .formSection:last-child{border-bottom:none}.formContainer .characterForm .formSection h2{font-size:1.25rem;color:var(--text-color);margin-top:0;margin-bottom:1.25rem}.formContainer .characterForm .formSection .formGroup{margin-bottom:1.25rem}.formContainer .characterForm .formSection .formGroup:last-child{margin-bottom:0}.formContainer .characterForm .formSection .formGroup label{display:block;font-weight:500;margin-bottom:.5rem}.formContainer .characterForm .formSection .formGroup .formInput{width:100%;padding:.75rem;border:1px solid #d1d5db;border-radius:4px;font-size:1rem}.formContainer .characterForm .formSection .formGroup .formInput:focus{outline:none;border-color:#3b82f6;box-shadow:0 0 0 3px rgba(59,130,246,.2)}.formContainer .characterForm .formSection .formGroup .formHint{margin-top:.5rem;font-size:.875rem;color:#6b7280}.formContainer .characterForm .formSection .formGroup .formHint pre{background-color:#f9fafb;padding:.75rem;border-radius:4px;overflow-x:auto}.formContainer .characterForm .formSection .formGroup .formHint summary{cursor:pointer;color:#3b82f6}.formContainer .characterForm .formSection .formRow{display:flex;gap:1rem}@media(max-width: 768px){.formContainer .characterForm .formSection .formRow{flex-direction:column;gap:1.25rem}}.formContainer .characterForm .formSection .formRow .formGroup{flex:1}.formContainer .characterForm .formActions{padding:1.5rem;background-color:#f9fafb;display:flex;justify-content:flex-end;gap:1rem}.formContainer .characterForm .formActions .submitButton,.formContainer .characterForm .formActions .cancelButton{padding:.75rem 1.5rem;border-radius:4px;font-weight:500;transition:all .2s ease}.formContainer .characterForm .formActions .submitButton{background-color:#3b82f6;color:#fff;border:none;cursor:pointer}.formContainer .characterForm .formActions .submitButton:hover{background-color:#2563eb}.formContainer .characterForm .formActions .submitButton:disabled{background-color:#93c5fd;cursor:not-allowed}.formContainer .characterForm .formActions .cancelButton{background-color:#fff;color:#4b5563;border:1px solid #d1d5db;display:flex;align-items:center;text-align:center}.formContainer .characterForm .formActions .cancelButton:hover{background-color:#f3f4f6;text-decoration:none}@keyframes fadeIn{from{opacity:0;transform:translateY(10px)}to{opacity:1;transform:translateY(0)}}.formContainer{animation:fadeIn .3s ease-out}